import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from "@/views/HomeView.vue";
import TourView from "@/views/TourView.vue";
import virRealView from "@/views/virRealView.vue";
import AllRequests from "@/views/AllRequests.vue";
import AchievementsView from "@/views/AchievementsView.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'default',
    component: HomeView,
    meta: {
      label: 'Главная',
      needAuth: false,
      hideMenu: false,
    },
  },
  {
    path: '/all-requests',
    name: 'all-requests',
    component: AllRequests,
    meta: {
      label: 'Квесты',
      needAuth: true,
      hideMenu: false,
    },
  },
  {
    path: '/quest',
    name: 'quest',
    component: TourView,
    meta: {
      needAuth: true,
      hideMenu: true,
    },
  },
  {
    path: '/achievements',
    name: 'achievements',
    component: AchievementsView,
    meta: {
      label: 'Достижения',
      needAuth: false,
      hideMenu: false,
    },
  },
  {
    path: '/bonuses',
    name: 'bonuses',
    component: HomeView,
    meta: {
      label: 'За бонусы',
      needAuth: false,
      hideMenu: false,
    },
  },
  {
    path: '/profile',
    name: 'profile',
    component: HomeView,
    meta: {
      label: 'Профиль',
      needAuth: false,
      hideMenu: false,
    },
  },

  {
    path: '/scanner',
    name: 'scanner',
    component: virRealView,
    meta: {
      needAuth: false,
      hideMenu: true,
    },
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
