import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('myToken') || 'unknown',
    tokenRefresh: localStorage.getItem('myTokenRefresh') || 'unknown',
    role: localStorage.getItem('myRole') || 'unknown',
    profile: {},
  },
  getters: {
    getToken: state => state.token,
    getTokenRefresh: state => state.tokenRefresh,
    getRole: state => state.role,
    getProfile: state => state.profile,
  },
  mutations: {
    SET_TOKEN: (state, resp) => {
      Vue.set(state, 'token', resp)
      localStorage.setItem('myToken', resp)
    },
    SET_TOKEN_REFRESH: (state, resp) => {
      Vue.set(state, 'tokenRefresh', resp)
      localStorage.setItem('myTokenRefresh', resp)
    },
    SET_ROLE: (state, resp) => {
      Vue.set(state, 'role', resp)
      localStorage.setItem('myRole', resp)
    },
    SET_PROFILE: (state, resp) => {
      Vue.set(state, 'profile', resp)
    },
  },
  actions: {
    SET_TOKEN: ({ commit, dispatch, getters }, string) => {
      commit('SET_TOKEN', string)
    },
    SET_TOKEN_REFRESH: ({ commit, dispatch, getters }, string) => {
      commit('SET_TOKEN_REFRESH', string)
    },
    SET_ROLE: ({ commit, dispatch, getters }, string) => {
      commit('SET_ROLE', string)
    },
    SET_PROFILE: ({ commit, dispatch, getters }, object) => {
      commit('SET_PROFILE', object)
    },
  },
  modules: {
  }
})
