<template>
  <div class="vr-container">
    <a-scene mindar-image="imageTargetSrc: /img/testvr/box.mind;" color-space="sRGB" renderer="colorManagement: true, physicallyCorrectLights" vr-mode-ui="enabled: false" device-orientation-permission-ui="enabled: false">
      <a-assets>
        <a-asset-item id="avatarModel" src="https://cdn.jsdelivr.net/gh/hiukim/mind-ar-js@1.2.5/examples/image-tracking/assets/card-example/softmind/scene.gltf"></a-asset-item>
      </a-assets>

      <a-camera position="0 0 0" look-controls="enabled: false"></a-camera>

      <a-entity id="example-target" mindar-image-target="targetIndex: 0">
        <a-gltf-model rotation="0 0 0 " position="0 0 0.1" scale="0.005 0.005 0.005" src="#avatarModel"
                      animation="property: position; to: 0 0.1 0.1; dur: 1000; easing: easeInOutQuad; loop: true; dir: alternate"
        ></a-gltf-model>
      </a-entity>
    </a-scene>
  </div>
</template>

<script>
export default {
  name: "VirRealView",
  mounted() {
    var self = this
    document.addEventListener("DOMContentLoaded", function () {
      const sceneEl = document.querySelector('a-scene');
      let arSystem;
      sceneEl.addEventListener('loaded', function () {
        arSystem = sceneEl.systems["mindar-image-system"];
      });
      const exampleTarget = document.querySelector('#example-target');
      // detect target found
      exampleTarget.addEventListener("targetFound", event => {
        self.$router.push('/')
        self.$root.changeBonus=true
      });
      // detect target lost
      exampleTarget.addEventListener("targetLost", event => {
        console.log("target lost");
      });
    });
  }
}
</script>

<style lang="scss" scoped>
.vr-container{
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  top:0;
}
</style>