import Vue from 'vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import '../public/scss/style.scss'
import { directive as onClickaway } from 'vue-clickaway'
import axios from "axios";
import navigationComponent from "@/components/navigationComponent.vue";

Vue.directive('on-clickaway', onClickaway)

Vue.config.productionTip = false

axios.defaults.baseURL = 'http://62.217.178.170'
Vue.prototype.$http = axios

axios.interceptors.request.use(
    (config) => {
      if (localStorage.getItem('myToken')&&localStorage.getItem('myToken')!=='unknown') {
        config.headers.authorization = `Bearer ${localStorage.getItem('myToken')}`;
      }
      return config;
    },
    (error) => Promise.reject(error)
);

// Response interceptor
axios.interceptors.response.use(
    (config) => {

      return config;
    },async (error) => {
      if (error.response.status == 401) {
        if(error.response.data.errors&&error.response.data.errors.length&&error.response.data.errors[0].message ==='Token expired.'){
          const request={'refresh_token':localStorage.getItem('myTokenRefresh')}
          axios.post('/auth/refresh',request).then((res)=>{
            console.log('res',res)
            localStorage.setItem('myToken', res.data.data.access_token)
            localStorage.setItem('myTokenRefresh', res.data.data.refresh_token)
          }).catch((e)=>{
            localStorage.removeItem('myToken')
            localStorage.removeItem('myTokenRefresh')
            window.open('/', "_self")
          })
        } else {
          localStorage.removeItem('myToken')
          localStorage.removeItem('myTokenRefresh')
          window.open('/', "_self")
        }
      }
    })

new Vue({
  el: '#app-main',
  router,
  store,
  data () {
    return {
      menu:[],
      changeBonus:false,
    }
  },
  components: {navigationComponent},
  computed: {
    tokenRefresh () {
      return this.$store.getters.getTokenRefresh
    },
    token () {
      return this.$store.getters.getToken
    },
    role () {
      return this.$store.getters.getRole
    },
  },
  watch:{
    $route () {
      setTimeout(()=> {
        window.scrollTo({top: 0})
      }, 1)
    },
    changeBonus(newValue){
      if(newValue)alert('Бонусы зачислены!')
    }
  },
  updated() {

  },
  mounted() {


    this.menu=[
      {name:'Главная', icon:'/img/menu/home.svg', path:'/', value:''},
      {name:'Миссии', icon:'/img/menu/quests.svg', path:'/all-requests'},
      {name:'Достижения', icon:'/img/menu/achievements.svg', path:'/achievements'},
      {name:'За бонусы', icon:'/img/menu/bonuses.svg', path:'/bonuses'},
      {name:'Профиль', icon:'/img/menu/profile.svg', path:'/profile'},
    ]
   if(!this.token||this.token==='unknown') this.getAuth()
   else axios.get('/users/me?fields=*.*').then((res) => {
     this.$store.dispatch('SET_PROFILE', res.data.data)
     this.$store.dispatch('SET_ROLE', res.data.data.role.name)
   })

  },
  methods: {
    getAuth(){
      axios.post('/auth/login', {'email':'vasya.pupkin@mail.ru', 'password':'1234'}).then((res)=>{
        this.$store.dispatch('SET_TOKEN', res.data.data.access_token)
        this.$store.dispatch('SET_TOKEN_REFRESH', res.data.data.refresh_token)
        window.open('/', "_self")
      })
    }
  },
})
